export default {
	data() {
		return {
			
		};
	},
	onLoad() {
		
	},
	onShow() {
		if(!this.getUserInfo()){
			
			uni.$once('login_cancel',(data) => {
					uni.switchTab({
						url:"/pages/video/main"
					});
			});
			let page = "/pages/add/add";
			this.toPage("/pages/login/login?page="+page);
			return;
		}else{
			uni.navigateTo({
				url:"/pages/add/add",
				animationType:'slide-in-bottom'
			});
		}
	},
	onHide() {
		
	},
	methods: {
		
	}
};
